<template>
  <div class="herd">
    <!-- 没有登录 -->
    <div v-if="JSON.stringify(user) == '{}'" class="felx-box">
      <div style="width: 10rem" class="felx-box">
        <div class="img"></div>
        <div>
          <el-button round type="ifno" @click="goMainView"> 首页</el-button>
        </div>
      </div>
      <!-- 查询功能没有完成 -->
      <div style="width: 42rem" class="felx-box">
        <div style="">
          <el-button type="ifno" @click="qie" style="margin-right: 2rem"
            >切换查询</el-button
          >
        </div>
        <div v-if="queryType" class="max-width">
          <el-input placeholder="请输入留言内容">
            <i slot="prefix" class="el-input__icon el-icon-chat-dot-square"></i>
          </el-input>
        </div>
        <div v-else class="max-width">
          <el-input placeholder="请输入用户名或昵称">
            <i slot="prefix" class="el-input__icon el-icon-user"></i>
          </el-input>
        </div>
      </div>
      <!-- 用户头像 -->
      <div style="width: 10rem">
        <div @click="login">
          <el-avatar> 登录 </el-avatar>
        </div>
      </div>
    </div>
    <!-- 登录后 -->
    <div v-else class="felx-box">
      <div style="width: 10rem" class="felx-box">
        <div class="img"></div>
        <div>
          <el-button round type="ifno" @click="goMainView"> 首页</el-button>
        </div>
      </div>
      <!-- 查询功能没有完成 -->
      <div style="width: 42rem" class="felx-box">
        <div style="">
          <el-button type="ifno" @click="qie" style="margin-right: 2rem"
            >切换查询</el-button
          >
        </div>
        <div v-if="queryType" class="max-width">
          <el-input placeholder="请输入留言内容">
            <i slot="prefix" class="el-input__icon el-icon-chat-dot-square"></i>
          </el-input>
        </div>
        <div v-else class="max-width">
          <el-input placeholder="请输入用户名或昵称">
            <i slot="prefix" class="el-input__icon el-icon-user"></i>
          </el-input>
        </div>
      </div>
      <!-- 用户头像 -->
      <div style="width: 10rem; margin-top: 10px">
        <div>
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link">
              <el-avatar :src="userImg"></el-avatar>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-user" command="myPage">
                我的主页</el-dropdown-item
              >
              <el-dropdown-item icon="el-icon-warning" command="out"
                >安全退出</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <!-- {{ user }} -->
  </div>
</template>

<script>
import tools from '@/js/tools';
export default {
  data() {
    return {
      queryType: true
    };
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
    userImg() {
      return this.$store.state.user.tbUserInfo.img;
    }
  },
  methods: {
    goMainView() {
      this.$router.push('/');
    },
    qie() {
      this.queryType = !this.queryType;
    },
    login() {
      this.$router.push('/user/login');
    },
    handleCommand(command) {
      if (command === 'out') {
        this.$confirm('确定将用户退出嘛？').then(() => {
          tools.ajax('/user/auth/logout', {}, (data) => {
            console.log(data.success);
            this.$store.dispatch('updatedUser');
            this.$message('用户已经安全退出');
            this.$router.push('/');
          });
        });
      } else if (command === 'myPage') {
        if (location.href.includes('/user/main/')) {
          this.$router.push({
            path: '/user/main/' + this.$store.state.user.tbUser.username
          });
          location.reload();
        } else {
          this.$router.push({
            path: '/user/main/' + this.$store.state.user.tbUser.username
          });
        }
      }
    }
  }
};
</script>

<style scoped>
.herd {
  background-color: rgba(00, 0, 00, 0.2);
  height: 100%;

  box-shadow: 0 0 12px 0 black;
}
.img {
  background-image: url('../assets/log.png');
  background-size: 100% 100%;
  width: 4rem;
  height: 100%;
}
.felx-box {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
