<template>
  <div>
    <div>
      <el-container>
        <el-header style="height: 4.2rem">
          <UserTop />
        </el-header>
        <!-- if折叠 -->
        <el-container class="main">
          <el-aside style="width: 240px" v-if="IsMy">
            <el-col :span="12">
              <el-menu
                default-active="1"
                class="el-menu-vertical-demo"
                background-color="#545c64"
                text-color="#fff"
                active-text-color="#ffd04b"
              >
                <el-menu-item index="1" @click="userInfoGet">
                  <i class="el-icon-user-solid"></i>
                  <span slot="title">用户信息与留言</span>
                </el-menu-item>
                <!-- 没写完 -->
                <el-menu-item index="2" @click="userInfoUpdate">
                  <i class="el-icon-setting"></i>
                  <span slot="title">用户附加信息修改</span>
                </el-menu-item>
                <el-menu-item index="3" @click="userPasswordUpdate">
                  <i class="el-icon-edit"></i>
                  <span slot="title">用户密码修改</span>
                </el-menu-item>
                <el-menu-item index="4" @click="userEmailUpdate">
                  <i class="el-icon-message"></i>
                  <span slot="title">邮箱信息修改</span>
                </el-menu-item>
                <el-menu-item index="5" @click="userPhoneUpdate">
                  <i class="el-icon-mobile"></i>
                  <span slot="title">手机信息修改</span>
                </el-menu-item>
              </el-menu>
            </el-col>
          </el-aside>
          <!-- 主要内容 -->
          <el-main>
            <router-view> </router-view>
          </el-main>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
import UserTop from '@/components/UserTop';
import tools from '@/js/tools.js';
export default {
  components: {
    UserTop
  },
  data() {
    return {
      userInfo: {
        tbUser: {},
        tbUserInfo: {},
        userOtherInfo: {}
      },
      IsMy: false
    };
  },
  methods: {
    // 调转至用户首页
    userInfoGet() {
      this.$router.push({
        path: '/user/main/' + this.userInfo.tbUser.username + '/userInfo'
      });
    },
    userPasswordUpdate() {
      this.$router.push({
        path: '/user/main/' + this.userInfo.tbUser.username + '/password'
      });
    },
    userEmailUpdate() {
      this.$router.push({
        path: '/user/main/' + this.userInfo.tbUser.username + '/email'
      });
    },
    userPhoneUpdate() {
      this.$router.push({
        path: '/user/main/' + this.userInfo.tbUser.username + '/phone'
      });
    },
    userInfoUpdate() {
      this.$router.push({
        path: '/user/main/' + this.userInfo.tbUser.username + '/update'
      });
    }
  },
  created() {
    if (tools.isLogin(this, this.$route.params.username)) {
      this.userInfo = this.$store.state.user;
      this.IsMy = true;
      this.userInfoGet();
    } else {
      tools.ajax(
        '/user/auth/getUserInfoByName',
        {
          username: this.$route.params.username
        },
        (data) => {
          this.userInfo.tbUser = data.tbUser;
          this.userInfo.tbUserInfo = data.tbUserInfo;
          this.userInfo.userOtherInfo = data.userOtherInfo;
          this.userInfoGet();
        }
      );
    }
  },

};
</script>

<style>
.el-header {
  padding: 0px;
}
.main {
  height: 90vh;
}
.el-col-12 {
  width: 100%;
  height: 100%;
  background-color: #545c64;
}
</style>
